import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import { changeOrderStatusApi } from "student/http";
import { rmAdminOrderTable } from "student/store/table";
import { useDispatch, useSelector } from "react-redux";

const AwaitForm = ({order = {}}) => {
  const { user } = useSelector((state) => state.auth);

  const dispatch = useDispatch();
  const [values, setValues] = useState({ message: "", subject: "" });

  const submitHandler = async () => {
     if(values.message === '' || values.subject ==='') return
    try{
     const res = await changeOrderStatusApi(order._id, { status: "await", values, sender: user });
     dispatch(rmAdminOrderTable(order._id));
     console.log(res);
    }catch(err){
        console.log(err);
    }
  };

  return (
    <div>
      <Form>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Subject</Form.Label>
          <Form.Control
            type="text"
            placeholder="Subject"
            value={values.subject}
            onChange={(e) => setValues({ ...values, subject: e.target.value })}
          />
          <Form.Text className="text-muted">enter subject.</Form.Text>
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicMessage">
          <Form.Label>Message</Form.Label>
          <Form.Control
            as="textarea"
            placeholder="Message"
            value={values.message}
            onChange={(e) => setValues({ ...values, message: e.target.value })}
          />
        </Form.Group>
        <Button variant="primary" type="button" onClick={submitHandler}>
          Submit
        </Button>
      </Form>
    </div>
  );
};

export default AwaitForm;
