import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Reset from "student/pages/reset/Reset";
import Forgot from "student/pages/forgot/Forgot";
import Layout from "student/admin/pages";
import Login from "student/admin/pages/login/Login";
import { useSelector } from "react-redux";
import adminRoutes from "student/admin/adminRoutes";
import subAdminRoutes from "./subAdmin/subAdminRoutes";

const SelectAdmin = () => {
  const { isAdminAuth, isSubAdminAuth } = useSelector((state) => state.auth);
  return isAdminAuth && !isSubAdminAuth ? (
    <Layout routers={adminRoutes} />
  ) : !isAdminAuth && isSubAdminAuth ? (
    <Layout routers={subAdminRoutes} />
  ) : (
    <Layout />
  );
};

const Admin = () => {
  return (
    <Switch>
      <PublicRouts path="/admin/login">
        <Login isAdmin={true} />
      </PublicRouts>
      <PrivateAdminRouet path="/admin">
        <SelectAdmin />
      </PrivateAdminRouet>
      <PublicRouts path="/admin/forgot">
        <Forgot />
      </PublicRouts>
      <ForgotRouts path="/admin/reset">
        <Reset />
      </ForgotRouts>
      <Route path="/admin">
        <Redirect to="/admin" />
      </Route>
    </Switch>
  );
};

const PrivateAdminRouet = ({ children, ...rest }) => {
  const { isAdminAuth, isSubAdminAuth, isAuth } = useSelector(
    (state) => state.auth
  );
  return (
    <Route
      {...rest}
      render={({ location }) => {
        return (isAdminAuth || isSubAdminAuth) && !isAuth ? (
          children
        ) : !(isAdminAuth || isSubAdminAuth) && isAuth ? (
          <Redirect
            to={{
              pathname: "/user",
              state: { from: location },
            }}
          />
        ) : (
          <Redirect
            to={{
              pathname: "/admin/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

const ForgotRouts = ({ children, ...rest }) => {
  const { isAdminAuth, forgot, isSubAdminAuth } = useSelector(
    (state) => state.auth
  );

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !(isAdminAuth || isSubAdminAuth) && forgot ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/admin/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

const PublicRouts = ({ children, ...rest }) => {
  const { isAdminAuth, isSubAdminAuth, isAuth } = useSelector(
    (state) => state.auth
  );

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !(isAdminAuth || isSubAdminAuth) && !isAuth ? (
          children
        ) : (isAdminAuth || isSubAdminAuth) && !isAuth ? (
          <Redirect
            to={{
              pathname: "/admin",
              state: { from: location },
            }}
          />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

export default Admin;
