import React, { useState } from "react";
import {
  Col,
  Row,
  Button,
  Collapse,
  Card,
  Form,
  Tab,
  Nav,
  Container,
  InputGroup,
  DropdownButton,
  Dropdown,
  FormControl,
} from "react-bootstrap";

import { FaSearch, FaSyncAlt, FaPaste, FaTachometerAlt } from "react-icons/fa";
import { IconContext } from "react-icons";
import styles from "./DashBoard.module.css";

import Search from "student/components/Search/Search";
import SubAdminOrderTable from 'student/components/Table/SubAdminOrderTable/SubAdminOrderTabla'

import { Link, Switch, Route, NavLink, withRouter } from "react-router-dom";
import { Redirect, useHistory } from "react-router-dom";
const dRouters = [
  {
    name: "New Order",
    path: "/new",
    icon: <FaPaste />,
    Component: SubAdminOrderTable,
    status: "new",
  },
  {
    name: "Awaiting Order",
    path: "/await-order",
    icon: <FaTachometerAlt />,
    Component: SubAdminOrderTable,
    status: "await",
  },
  {
    name: "Paid & Await Order",
    path: "/paid-await-order",
    icon: <FaTachometerAlt />,
    Component: SubAdminOrderTable,
    status: "paidAwait",
  },
  {
    name: "Proccess Order",
    path: "/process-order",
    icon: <FaTachometerAlt />,
    Component: SubAdminOrderTable,
    status: "process",
  },
  {
    name: "Complete Order",
    path: "/complete-order",
    icon: <FaTachometerAlt />,
    Component: SubAdminOrderTable,
    status: "complete",
  },
];

const DashBoard = ({ location, match }) => {
  const history = useHistory();
  const [awaitValue, setAwaitValue] = useState("");

  const linkClickHandler = (state) => {
    setStatus(state);
  };
  const dropdownHandler = (v) => {
    setAwaitValue(v);
  };

  return (
    <div className={`${styles.bgLightWhite} position-relative`}>
      <Tab.Container id="left-tabs-dashboard-example">
        <div className={`${styles.darkBlue} rounded-top p-2 `}>
          <Search />
          <Row className="d-md-none mt-1">
            <Form.Group className="mb-3">
              <Form.Select size='sm'>
                <option>Filter select</option>
                {dRouters.map((e, i) => (
                  <option
                    onClick={() => history.push(`${match.path}${e.path}`)}
                    as={Link}
                    key={i}
                  >
                    {e.name}
                  </option>
                ))}
              </Form.Select>
            </Form.Group>
          </Row>
          <Row className="d-md-block d-none">
            <Container>
              <InputGroup size="sm" className="mb-1 mt-2 px-3 ">
                <DropdownButton
                  onChange={(e) => console.log("console", e)}
                  variant="outline-secondary"
                  title="AWAIT"
                  id="input-group-dropdown-1"
                >
                  <Dropdown.Item onClick={() => dropdownHandler("")}>
                    None
                  </Dropdown.Item>
                  <Dropdown.Divider />
                  <Dropdown.Item onClick={() => dropdownHandler("admin")}>
                    Adimn
                  </Dropdown.Item>
                  <Dropdown.Divider />
                  <Dropdown.Item onClick={() => dropdownHandler("user")}>
                    User
                  </Dropdown.Item>
                </DropdownButton>
                {/* <FormControl aria-label="Text input with dropdown button" value='thid'  /> */}
                <Form.Control
                  style={{ color: "transparent", textShadow: "0 0 0 #000000" }}
                  placeholder="Select Awaiting"
                  readOnly
                  value={awaitValue.toUpperCase()}
                />
              </InputGroup>
              <IconContext.Provider value={{ size: 25 }}>
                <Nav
                  variant="pills"
                  className={`${styles.tabNav} d-flex mb-3 `}
                >
                  {dRouters.map((e, i) => (
                    <Nav.Item key={i}>
                      <Nav.Link
                        as={NavLink}
                        to={`${match.path}${e.path}`}
                        className={`${styles.tabBtn}`}
                        activeClassName={styles.tabBtnActive}
                      >
                        {e.icon}
                        <br />
                        {e.name}
                      </Nav.Link>
                    </Nav.Item>
                  ))}
                </Nav>
              </IconContext.Provider>
            </Container>
          </Row>
        </div>
        <div className="tab-content mt-4">
          <Tab.Content>
            <Switch>
              {dRouters.map((E, i) => {
                return (
                  <Route key={i} path={`${match.path}${E.path}`}>
                    {E.Redirect ? (
                      <Redirect to={`${match.path}${E.path}${E.redirect}`} />
                    ) : (
                      <E.Component status={E.status} awaitValue={awaitValue} />
                    )}
                  </Route>
                );
              })}
              <Redirect to={`${match.path}/new`} />
            </Switch>
          </Tab.Content>
        </div>
      </Tab.Container>
    </div>
  );
};

export default withRouter(DashBoard);
