import React, { useEffect, useState } from "react";
import {
  Table,
  Pagination,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Button,
  Collapse,
  Card,
  Form,
} from "react-bootstrap";
import { FaRegEye, FaRegComments, FaSearch, FaSyncAlt } from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { IconContext } from "react-icons";
import { useDispatch } from "react-redux";
import { setOrder, setReceiver } from "student/store/order";
import { useSelector } from "react-redux";
import moment from "moment";

import styles from "./MessagesTable.module.css";
import { messageByDateApi } from "student/http";

const MessagesTable = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { user } = useSelector((s) => s.auth);
  const [open, setOpen] = React.useState(false);


  const [tableData, setTableData] = useState([]);
  const [totalData, setTotalData] = useState(5);
  const [isUnmount, setIsUnmount] = useState(false);

  const [pagData, setPagData] = useState({
    page: 0,
    row: 10,
  });
  const [search, setSearch] = useState({
    await: "",
    startDate: "",
    endDate: "",
    translation: "",
  });

  console.log("DATA", tableData);

  React.useEffect(() => {
    if (user._id) {
      (async () => {
        try {
          const { data } = await messageByDateApi(
            user._id,
            pagData,
            `endDate=${search.endDate}&startDate=${search.startDate}`
          );
          console.log(data, "dataaaa");
          if (data) {
            //   if(!isUnmount){
            if (data.data) {
              setTableData(data.data);
              setTotalData(data.total);
            }
            //   }
          }
        } catch (e) {
          console.log(e);
        }
      })();
    }
    return () => {
      setTableData([]);
      setIsUnmount(true);
    };
  }, [pagData, search]);
  const totalPages = Math.ceil(+totalData / +pagData.row);
  const pagNumber = [...Array(totalPages ?? 10).keys()] || [];

  const pagHandler = (a) => {
    if (a === "next") {
      if (pagData.page !== pagNumber.length - 1)
        setPagData({ ...pagData, page: +pagData.page + 1 });
    } else {
      if (pagData.page >= 1)
        setPagData({ ...pagData, page: +pagData.page - 1 });
    }
  };

  const viewClickHandler = (v) => {
    dispatch(setOrder(v));
    if (user.role !== "user") {
      dispatch(setReceiver(v.userId));
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/inner-order"
        : "/dashbord/inner-order";
    history.push(url);
  };
  const messageClickHandler = (v) => {
    dispatch(setOrder(v));
    if (user.role !== "user") {
      dispatch(setReceiver(v.userId));
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/chat-support"
        : "/dashbord/chat-support";
    history.push(url);
  };
  const setPagDataHandler = (v) => {
    setPagData({ ...pagData, page: v });
  };

  return (
    <div>
      <div className={`${styles.darkBlue} rounded-top p-2 mb-2 `}>
        <Row>
          <Col md={6}>
            <h4 className={`${styles.dashboard} text-white`}>Messages</h4>
          </Col>
          <Col md={6}>
            <div className={styles.btnRight}>
              <Button
                className={styles.dashBoardBtn}
                onClick={() => setOpen(!open)}
                aria-controls="example-collapse-text"
                aria-expanded={open}
              >
                <FaSearch />
              </Button>
            </div>
          </Col>
        </Row>
        <Collapse in={open} className="mt-2">
          <div id="example-collapse-text">
            <Card>
              <Card.Body>
                <Row>
                  <Col md={5}>
                    <Row>
                      <Col md={6} className="p-1">
                        <Form.Label>Start Date</Form.Label>
                        <Form.Control
                          placeholder="Enter startDate"
                          type="date"
                          onChange={(e) =>
                            setSearch({ ...search, startDate: e.target.value })
                          }
                        />
                      </Col>
                      <Col md={6} className="p-1">
                        <Form.Label>End Date</Form.Label>

                        <Form.Control
                          placeholder="Enter Date"
                          onChange={(e) =>
                            setSearch({ ...search, endDate: e.target.value })
                          }
                          type="date"
                        />
                      </Col>
                    </Row>
                  </Col>
                  <Col md={5}>
                    
                  </Col>
                  <Col md={2} className="m-0 px-2 py-1 row">
                    <Row>
                      <Col xs={6} md={6} className="m-0">
                        <Button className={styles.searchBtn}>
                          <FaSearch />
                        </Button>
                      </Col>
                      <Col xs={6} md={6}>
                        <Button className={styles.refreshBtn}>
                          <FaSyncAlt />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </div>
        </Collapse>
      </div>
      <div className="bg-white rounded overflow-scroll">
        <IconContext.Provider
          value={{ color: "#000", size: 20, paddingLeft: 10 }}
        >
          <Table striped className={styles.table}>
            <thead className={styles.tableInfo}>
              <tr className={styles.tableInfo}>
                <th>S.No</th>
                <th>Order Id</th>
                <th>Type</th>
                <th>message</th>
                <th>Date</th>
                <th>status</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody
              className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
            >
              {tableData.length ? (
                tableData.map((e, i) => {
                  return (
                    <tr key={e._id} className={styles.tableInfo}>
                      <td>{i + 1}</td>
                      <td onClick={() => viewClickHandler(e.info)}>
                        {e.orderId.translationId}
                      </td>
                      <td>{e.type}</td>
                      <td onClick={() => viewClickHandler(e.info)}>
                        {e.message}
                      </td>
                      <td>{moment(e.createdAt).calendar()}</td>
                      <td>{e.seen ? "seen" : "Pending"}</td>
                      {/* <td>{e.seen ? "seen" : "unseen"}</td> */}
                      <td>
                        <div className="d-flex">
                          <OverlayTrigger
                            placement="top"
                            overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                          >
                            <Button
                              variant="link"
                              className="p-1"
                              onClick={() => viewClickHandler(e.orderId)}
                            >
                              <FaRegEye />
                            </Button>
                          </OverlayTrigger>
                          <OverlayTrigger
                            placement="top"
                            overlay={
                              <Tooltip id={`tooltip-top`}>messages</Tooltip>
                            }
                          >
                            <Button
                              variant="link"
                              className="p-1"
                              onClick={() => messageClickHandler(e.orderId)}
                            >
                              <FaRegComments />
                            </Button>
                          </OverlayTrigger>
                        </div>
                      </td>
                    </tr>
                  );
                })
              ) : (
                <tr className={styles.tableInfo}>
                  <td colSpan="5" className="text-center bg-white">
                    There is no Any Order{" "}
                  </td>
                </tr>
              )}
            </tbody>
          </Table>
        </IconContext.Provider>

        <div className="p-1 m-2 d-flex flex-row justify-content-between">
          <p className="">
            Showing {+pagData.page * +pagData.row} to{" "}
            {(+pagData.page + 1) * pagData.row} of {totalData} entries
          </p>
          <Pagination className="">
            <Pagination.Item onClick={() => pagHandler("pre")}>
              {"Privious"}
            </Pagination.Item>
            {+pagData.page > 2 && (
              <>
                <Pagination.Item onClick={() => setPagDataHandler(0)}>
                  {1}
                </Pagination.Item>
                <Pagination.Ellipsis />
              </>
            )}

            {pagData.page > 1 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page - 2)}
              >
                {+pagData.page - 1}
              </Pagination.Item>
            )}
            {pagData.page > 0 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page - 1)}
              >
                {+pagData.page}
              </Pagination.Item>
            )}

            <Pagination.Item
              // onClick={() => setPagData({ ...pagData, page: +e })}
              active
            >
              {+pagData.page + 1}
            </Pagination.Item>
            {+pagData.page <= totalPages - 2 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page + 1)}
              >
                {+pagData.page + 2}
              </Pagination.Item>
            )}

            {+pagData.page <= totalPages - 3 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page + 2)}
              >
                {+pagData.page + 3}
              </Pagination.Item>
            )}

            {+pagData.page <= totalPages - 4 && (
              <>
                <Pagination.Ellipsis />
                <Pagination.Item
                  onClick={() => setPagDataHandler(+totalPages - 1)}
                >
                  {+totalPages}
                </Pagination.Item>
              </>
            )}

            <Pagination.Item onClick={() => pagHandler("next")}>
              {"Next"}
            </Pagination.Item>
          </Pagination>
        </div>
      </div>
    </div>
  );
};

export default MessagesTable;
