import { createSlice } from "@reduxjs/toolkit";

const tableSlice = createSlice({
  name: "table",
  initialState: {
    subAdmin: null,

    userTable: { data: [], total: 1 },
    orderTable: { data: [], total: 1 },
    adminOrderTable: { data: [], total: 1 },
    subAdminOrderTable: { data: [], total: 1 },
    orderOfUserTable: { data: [], total: 1 },
  },
  reducers: {
    setUserTable: (s, a) => {
      s.userTable = a.payload;
    },
    setOrderTable: (s, a) => {
      s.orderTable = a.payload;
    },
    setAdminOrderTable: (s, a) => {
      s.adminOrderTable = a.payload;
    },
    setSubAdminOrderTable: (s, a) => {
      s.subAdminOrderTable = a.payload;
    },
    rmAdminOrderTable: (s, a) => {
      s.adminOrderTable.data = s.adminOrderTable.data.filter(
        (e) => e._id !== a.payload
      );
    },
    setOrderOfUserTable: (s, a) => {
      s.orderOfUserTable = a.payload;
    },
    setSubAdmin: (s, a) => {
      s.subAdmin = a.payload;
    },
  },
});

export const {
  setOrderTable,
  setUserTable,
  setOrderOfUserTable,
  setAdminOrderTable,
  rmAdminOrderTable,
  setSubAdminOrderTable,
  setSubAdmin,
} = tableSlice.actions;

export default tableSlice.reducer;
