import {
  FaTachometerAlt,
  FaRegCommentDots,
  FaUserEdit,
  FaCartArrowDown,
  FaRegBell,
  FaRegAddressBook,
  FaComment,
} from "react-icons/fa";

import Dashboard from "student/admin/pages/Dashboard/DashBoard";
import ChatSupport from "student/pages/dashboard/ChatSupport/ChatSupport";
import AddOrder from "student/pages/dashboard/AddOrder/AddOrder";
import Notification from "student/pages/dashboard/Notification/Notification";
import AddUserForm from 'student/components/UserForm/AddUserForm/AddUserForm';
import UserTable from "student/components/Table/UserTable/UserTable";
import SubAdminTable from "student/components/Table/SubAdminTable/SubAdminTable";
import TableBoot from "student/components/Table/TableBoot";
import InnerOrder from "student/admin/pages/InnerOrder/InnerOrder";
import DLayout from "student/layout/DLayout";
import InnerUser from "student/admin/pages/InnerUser/InnerUser";
import Messages from "student/admin/pages/Messages/Messages";
import AdminOrderTable from "student/components/Table/AdminOrderTable/AdminOrderTabla";
import InnerSubAdmin from "student/admin/pages/InnerSubAdmin/InnerSubAdmin";
import EditUserForm from "student/components/UserForm/EditUserForm/EditUserForm";

const globlepath = "http://localhost:3000/admin/dashboard/order/";

const adminRoutes = [
  {
    name: "Dashbord",
    path: "/dashbord",
    icon: <FaTachometerAlt />,
    component: Dashboard,
  },
  {
    name: "Users",
    path: "/users",
    icon: <FaRegAddressBook />,
    children: [
      {
        name: "Client",
        path: "/client",
        icon: <FaTachometerAlt />,
        component: <UserTable />,
      },
      {
        name: "Sub Admin",
        path: "/sub-admin",
        icon: <FaTachometerAlt />,
        component: <SubAdminTable />,
      },
      {
        name: "Add New User",
        path: "/add-user",
        icon: <FaTachometerAlt />,
        component: <AddUserForm />,
      },
      {
      
        path: "/edit-user/:id",
        icon: <FaTachometerAlt />,
        component: <EditUserForm />,
      },
    ],
  },
  {
    name: "Order",
    path: "/order",
    icon: <FaUserEdit />,
    component: Dashboard,
    children: [
      {
        name: "New Order",
        path: "/new",
        icon: <FaTachometerAlt />,
      },
      {
        name: "Await Order",
        path: "/await-order",
        icon: <FaTachometerAlt />,
      },
      {
        name: "Paid & Await Order",
        path: "/paid-await-order",
        icon: <FaTachometerAlt />,
      },
      {
        name: "Process Order",
        path: "/process-order",
        icon: <FaTachometerAlt />,
      },
      {
        name: "Complete Order",
        path: "/complete-order",
        icon: <FaTachometerAlt />,
      },
    ],
  },
  {
    name: "Chat Support",
    path: "/chat-support",
    icon: <FaRegCommentDots />,
    component: ChatSupport,
  },
  {
    path: "/inner-order",
    icon: <FaRegBell />,
    component: InnerOrder,
  },
  {
    path: "/inner-user",
    icon: <FaRegBell />,
    component: InnerUser,
  },
  {
    path: "/inner-sub-admin",
    icon: <FaRegBell />,
    component: InnerSubAdmin,
  },
  {
    name: "Notification",
    path: "/notification",
    icon: <FaRegBell />,
    component: Notification,
  },
  {
    name: "Messages",
    path: "/messages",
    icon: <FaComment />,
    component: Messages,
  },
];

export default adminRoutes;
