import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import { changeOrderStatusApi } from "student/http";
import { rmAdminOrderTable } from "student/store/table";
import { useDispatch, useSelector } from "react-redux";
import { getTranslationUesrsApi, getSubAdminApi } from "student/http";
import { assignOrderApi } from "student/http";

const AwaitForm = ({ order = {} }) => {
  const { user } = useSelector((state) => state.auth);

  const dispatch = useDispatch();
  const [selectData, setSelectData] = useState([]);
  const [values, setValues] = useState({ message: "", subAdmin:null });
  const [pagData, setPagData] = useState({
    page: 0,
    row: 10,
  });

  useEffect(() => {
    (async() => {
      try {
        const { data } = await getSubAdminApi(pagData);
        if (data) {
          console.log('assign order data',data.data)
          if (data.data) {
            setSelectData(data.data);
          }
        }
      } catch (e) {
        console.log(e);
      }
    })();
    return () => {
      setSelectData([]);
      setValues({ message: "", subAdmin: null });
    };
  }, []);

   console.log('values', values)

  const submitHandler = async () => {
    if (values.subAdmin === null) return;
    console.log("subminttiong");

    try {
      const { data } = await assignOrderApi({order, user: values.subAdmin});
      if(data){
        console.log(data);
      }

      
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div>
      <Form>
        <Form.Group className="mb-3">
          <Form.Label>Select Sub-Admin</Form.Label>
          <Form.Select>
            <option >Select Sub-Admin</option>

            {selectData.map((e) => (
              <option key={e._id} onClick={()=>setValues({...values, subAdmin: e})}>{e?.email}</option>
            ))}
          </Form.Select>
          <Form.Text className="text-muted">select to assign order</Form.Text>
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicMessage">
          <Form.Label>Message</Form.Label>
          <Form.Control
            as="textarea"
            placeholder="Message"
            value={values.message}
            onChange={(e) => setValues({ ...values, message: e.target.value })}
          />
        </Form.Group>
        <Button variant="primary" type="button" onClick={submitHandler}>
          Submit
        </Button>
      </Form>
    </div>
  );
};

export default AwaitForm;
