import { useEffect, useState } from 'react'
import { Modal, Button} from 'react-bootstrap'



 export default function Example({ open = false, isDeleteHandler }) {
   const handleClose = () => isDeleteHandler(false);

   return (
     <>
       <Modal
         show={open}
         onHide={handleClose}
         backdrop="static"
         keyboard={false}
       >
         <Modal.Header closeButton>
           <Modal.Title>Delete Confirmation</Modal.Title>
         </Modal.Header>
         <Modal.Body>
           <h5>are you sure want to delete this client</h5>
         </Modal.Body>
         <Modal.Footer>
           <Button variant="secondary" onClick={handleClose}>
             Close
           </Button>
           <Button onClick={() => isDeleteHandler(true)} variant="danger">
             Delete
           </Button>
         </Modal.Footer>
       </Modal>
     </>
   );
 }